.recipe {
    border: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: #fff;
    align-items: center;
    width: 40%;
    padding-bottom: 20px;
    text-align: left;
}

.items {
    padding: 5px;
}

@media screen and (max-width: 900px) {
    .recipe {
        width: 100%;
    }
}
